import Vue from 'vue'
import Vuex from 'vuex'
import state from './state'
import Web3 from 'web3'
import { stat } from 'fs';

Vue.use(Vuex)

export default new Vuex.Store({
  state,
  getters: {
    acct(state) {
      return state.switchwtc ? state.acctwtc : state.acctdmtc
    }
  },
  mutations: {
    initWeb3(state, web3) {
      state.web3 = web3
    },
    setSwitchwtc(state, switchwtc) {
      state.switchwtc = switchwtc
    },
    setBalance(state, {
      address,
      eth,
      wtc,
      dmtc,
      loading,
    }) {
      let acct = state.switchwtc ? state.acctwtc : state.acctdmtc
      let acc = acct.find(acc => acc.address === address)
      acc.eth = eth ? eth : acc.eth
      acc.wtc = wtc ? wtc : acc.wtc
      acc.dmtc = dmtc ? dmtc : acc.dmtc
      acc.loading = (loading != undefined) ? loading : acc.loading
    },
  },
  actions: {
    checkInitWeb3({
      commit,
      state
    }) {
      if (state.web3) {
        return state.web3
      }
      let web3 = new Web3(new Web3.providers.HttpProvider("https://mainnet.infura.io/v3/97ff95c5383842559ef8cfe1346ab8ba"))
      let abi = [{"constant":true,"inputs":[{"name":"_owner","type":"address"}],"name":"balanceOf","outputs":[{"name":"","type":"uint256"}],"payable":false,"type":"function"}]
      let contractdmtc = web3.eth.Contract(abi, '0x3be6e7bF2cD8E1a0A95597E72ca6D3709bBeFF76')
      let contractwtc = web3.eth.Contract(abi, '0xb7cB1C96dB6B22b0D3d9536E0108d062BD488F74')
      commit('initWeb3', {
        web3,
        contractwtc,
        contractdmtc,
      })
    },
    getBalance({
      commit,
      state
    }, {
      address
    }) {
      commit('setBalance', {
        address,
        loading: true
      })
      let {web3, contractdmtc, contractwtc} = state.web3
      let dmtc = 0
      let wtc = 0
      let eth = 0
      contractdmtc.methods.balanceOf(address).call().then(r => {
        dmtc = r / 10 ** 18
        commit('setBalance', {
          address,
          loading: false
        })
        commit('setBalance', {
          address,
          dmtc,
        })
      })
      contractwtc.methods.balanceOf(address).call().then(r => {
        wtc = r / 10 ** 18
        commit('setBalance', {
          address,
          loading: false
        })
        commit('setBalance', {
          address,
          wtc,
        })
      })
      web3.eth.getBalance(address).then(r => {
        eth = r / 10 ** 18
        commit('setBalance', {
          address,
          loading: false
        })
        commit('setBalance', {
          address,
          eth,
        })
      })
    }
  }
})