<template>
  <v-content>
    <v-container fluid fill-height>
      <v-layout justify-center align-top>
        <!-- table -->
        <v-flex>
          <v-card class="pb-3 mb-5">
            <v-card-title>
              <!-- <v-switch v-model="switchCoin" label="list wtc fundation"></v-switch> -->
              <v-switch v-model="switchCoin" :label="`list ${coinType}`"></v-switch>
              <v-text-field v-model="search" append-icon="search" label="Search"></v-text-field>
              <v-btn small icon class="warning mx-5" @click="getAllBalance">
                <v-icon>cached</v-icon>
              </v-btn>
            </v-card-title>
            <v-data-table
              :headers="table.headers"
              :items="table.items"
              :search="search"
              :rows-per-page-items="[80, 40, 20, 10]"
              :loading="false"
              class="elevation-2 mx-3"
            >
              <v-progress-linear v-slot:progress color="blue" indeterminate></v-progress-linear>
              <template v-slot:items="props">
                <td class="text-xs-center">{{ props.item.name }}</td>
                <td class="text-xs-center">{{ props.item.coin }}</td>
                <td class="text-xs-left">{{ props.item.address }}</td>
                <td class="text-xs-right">{{ props.item.eth | formatNumber(2,true) }}</td>
                <td class="text-xs-right">{{ props.item.wtc | formatNumber(2,true) }}</td>
                <td class="text-xs-right">{{ props.item.dmtc | formatNumber(2,true) }}</td>
                <td class="text-xs-center">
                  <v-btn
                    small
                    flat
                    icon
                    class="warning"
                    @click="getBalance(props.item.address)"
                    :loading="props.item.loading"
                  >
                    <v-icon>cached</v-icon>
                  </v-btn>
                </td>
              </template>
            </v-data-table>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </v-content>
</template>

<script>
export default {
  data() {
    return {
      item: this.$store.state.web3,
      gasPrice: 0,
      loading: false,
      bal: 0,
      acct: 0,
      table: {},
      search: "",
      switchCoin: true, // true: wtc, false: dmtc
      coinType: "wtc fundation"
    };
  },
  filters: {
    /**
     * 将数值四舍五入后格式化.
     * @param num 数值(Number或者String)
     * @param cent 要保留的小数位(Number)
     * @param isThousand 是否需要千分位 0:不需要,1:需要(数值类型);
     * @return 格式的字符串,如'1,234,567.45'
     * @type String
     */
    formatNumber(num, cent, isThousand) {
      num = num.toString().replace(/\$|\,/g, "");

      // 检查传入数值为数值类型
      if (isNaN(num)) num = "0";

      // 获取符号(正/负数)
      let sign = num == (num = Math.abs(num));

      num = Math.floor(num * Math.pow(10, cent) + 0.50000000001); // 把指定的小数位先转换成整数.多余的小数位四舍五入
      let cents = num % Math.pow(10, cent); // 求出小数位数值
      num = Math.floor(num / Math.pow(10, cent)).toString(); // 求出整数位数值
      cents = cents.toString(); // 把小数位转换成字符串,以便求小数位长度

      // 补足小数位到指定的位数
      while (cents.length < cent) cents = "0" + cents;

      if (isThousand) {
        // 对整数部分进行千分位格式化.
        for (var i = 0; i < Math.floor((num.length - (1 + i)) / 3); i++)
          num =
            num.substring(0, num.length - (4 * i + 3)) +
            "," +
            num.substring(num.length - (4 * i + 3));
      }

      if (cent > 0) return (sign ? "" : "-") + num + "." + cents;
      else return (sign ? "" : "-") + num;
    }
  },
  watch: {
    switchCoin() {
      if (this.switchCoin) {
        // switch to wtc
        this.$store.commit("setSwitchwtc", true);
        this.coinType = "wtc fundation";
      } else {
        this.$store.commit("setSwitchwtc", false);
        this.coinType = "dmtc fundation";
      }
      this.updateTable();
    }
  },
  computed: {},
  methods: {
    getBalance(address) {
      this.$store.dispatch("getBalance", { address });
    },
    getAllBalance() {
      let that = this
      this.$store.getters.acct.forEach(({address}) => {
        that.$store.dispatch('getBalance', { address })
      });
    },
    updateTable() {
      this.table = {
        items: this.$store.getters.acct,
        headers: [
          "name",
          "coin",
          "address",
          "eth",
          "wtc",
          "dmtc",
          "updatae"
        ].map(c => {
          return { text: c, value: c, align: 'center' };
        })
      };
    }
  },
  mounted() {
    this.$store.dispatch("checkInitWeb3");
    this.updateTable();
    // this.getAllBalance()
  }
};
</script>